*{
  margin: 0;
 }
 
 .App {
  max-width: 900px;
  margin: 6rem auto; 
  background-color: #f4f4f4;
  height: 90vh;
  position: relative;
  /* background-image: url(assets/images/mountains-.jpg); */
}

.zee-form-container, .login-form, .register-form{
  display: flex;
  flex-direction:column;
  
}

@media screen and (min-width: 600px) {

}.zee-form-container{
  padding: 2rem;
  border: 1px solid white;
  border-radius: 10px;
  margin: 2rem;
  background-image: url(assets/images/surface.jpg);
}

label {
  text-align: left;
  padding: 0.25rem 0;
}

input {
  margin: 0.5rem 0;
  padding: 1rem;
  border: none;
  border-radius: 10px;
}

button {
  border: none;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
  color: #7439db;
}

.link-btn{
 background-color: none; 
 color: rgb(17, 12, 153);
 text-decoration: underline;

}